@charset "utf-8";

/* the name image font is Intens Writings by Intense from dafont.com */

// Our variables
$base-font-family: "Source Sans Pro", sans-serif;
$read-font-family: 'Palatino Linotype', 'Book Antiqua', Palatino, serif;
$base-font-size:   16px;
$base-font-weight: 400;
$small-font-size:  $base-font-size * 0.875;
$base-line-height: 1.5;

$spacing-unit:     30px;

$text-color:       #111;
$background-color: #fdfdfd;
$brand-color:      #990000;

$grey-color:       #93aaa9;
$grey-color-light: lighten($grey-color, 40%);
$grey-color-dark:  darken($grey-color, 25%);

// Width of the content area
$content-width:    800px;

$on-palm:          600px;
$on-laptop:        800px;



// Use media queries like this:
// @include media-query($on-palm) {
//     .wrapper {
//         padding-right: $spacing-unit / 2;
//         padding-left: $spacing-unit / 2;
//     }
// }
@mixin media-query($device) {
    @media screen and (max-width: $device) {
        @content;
    }
}



// Import partials from `sass_dir` (defaults to `_sass`)
@import
        "base",
        "layout",
        "syntax-highlighting"
;

// ~~~~ custom ~~~~

.post-content {
  font-family: $read-font-family;
  font-size:1.2em;
}

.post-list p {
  padding-left: 2em;
  padding-right: 2em;
  font-family: $read-font-family;
}

.post-subtitle {
	font-style: italic;
	color: #828282;
}



.site-header {
  background-color: $background-color;
  min-height:10em;
  position: relative;
  z-index:3;
}

a:visited {
  color: $grey-color;
}

a, 
a:link,
a:active {
  color: $grey-color-dark;
}


footer a,
footer a:link,
footer a:active {
  color: $grey-color-dark;
}

.site-header a, 
.site-header a:visited,
.site-header a:link,
.site-header a:active {
  color: $grey-color-dark;
}

.site-header a:hover {
  color: $grey-color;
}

.site-title, .post-title {
}

.post-title {
  font-family: $read-font-family;
}

.slide {
  text-align:left;
}

.teaser_more {
  text-align:right;
}

figure {
	background-color:#e0e0e0;
	border:0.1em solid #ccc;
	-moz-border-radius: 0.3em;
	border-radius: 0.3em;
	margin:4em 0em 0.5em 0em;
	padding:0.5em 0.5em 0.5em 0.5em;
	text-align:center;
	width: 100%;
}

figure img {
	border:1px solid #e0e0e0;
	max-width:99%;
	-moz-border-radius: 0.3em;
	border-radius: 0.3em;
	display: inline-block !important;
}

figcaption {
	text-align:center;
	font-size:0.9em;
	line-height:1.2em;
	margin:1em 0 0 0;
}

iframe,
video,
embed,
object {
	/* border:0.2em solid #ccc;*/
	-moz-border-radius: 0.3em;
	border-radius: 0.3em;
	text-align:center;
	width:100%;
}


/* imported from previous site */
#gallery li {
  display:inline-block;
  background-color:#e0e0e0;
  border:0.1em solid #ccc;
  -moz-border-radius: 0.3em;
  border-radius: 0.3em;
  margin:0.5em 0em 0.5em 0em;
  padding:0.5em 0.5em 0.5em 0.5em;  
}

#gallery img {
  height:127px;
  width:127px;
}

#gallery_vertical li {
  display:inline-block;
  background-color:#e0e0e0;
  border:0.1em solid #ccc;
  -moz-border-radius: 0.3em;
  border-radius: 0.3em;
  margin:0.5em 0em 0.5em 0em;
  padding:0.5em 0.5em 0.5em 0.5em;  
}

#gallery_vertical img {
  width:127px;
  height:226px;
}

